<template>
	<div class="bg">
	<md-button class="md-icon-button md-fab-top-left md-fab md-mini testi md-primary">
						<md-tooltip md-delay="100" md-direction="right">For testing you can try user1/password1 and user2/password2</md-tooltip>
							<md-icon style="color:white">info</md-icon>
						</md-button>
		<div class="Login">
			<md-whiteframe md-elevation="24" class="limiter md-primary">
						
				<span class="md-display-3 logheading">Do<span class="loghead2">To</span></span>
				
				<form novalidate @submit.stop.prevent="submit" class="myForm" @keyup.enter="checklog">
					<transition name="bounce" mode="out-in">
						<span v-show="validinfo" class="md-subheading alert">{{message}}</span>
					</transition>


					<md-input-container>
						<label>Username</label>
						<md-input v-model="username"></md-input>
					</md-input-container>

					<md-input-container md-has-password>
						<label>Password</label>
						<md-input type="password"  v-model="password"></md-input>
					</md-input-container>
					
					<md-layout md-gutter class="options">
						<md-layout md-flex="25" md-flex-offset="5"><md-checkbox id="my-test1" name="my-test1" class="md-primary" v-model="checkbox" >Remember me</md-checkbox></md-layout>
						<md-layout md-flex="25" md-flex-offset="25"><md-button class="md-raised md-dense md-primary" @click="checklog">Login</md-button></md-layout>
					</md-layout>

					
					
				</form>

				<md-layout md-gutter >
					<md-layout md-flex="25" md-flex-offset="25"><span class="md-body-1">Register</span></md-layout>
					<md-layout md-flex="25" md-flex-offset="9"><span class="md-body-1" >Forgot Password?</span></md-layout>
				</md-layout>



			</md-whiteframe>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				username:'',
				password:'',
				checkbox:'',
				validinfo:false,
				data:null,
				message:'Please check your login info'
			}
		},
		methods: {
			checklog(){
				validinfo:false;
				for (var i = 0; i < this.data.body.length; i++) {
					console.log(this.data.body[i].login.username)
					if(this.data.body[i].login.username == this.username && this.data.body[i].login.password == this.password){
						localStorage.id = this.data.body[i].id
						this.$router.push({ name: 'main' ,params:{id:this.data.body[i].id}})

					}else if(this.data.body[i].login.username == this.username){
						this.message='Incorrect password'
						this.validinfo=true
					}else{
						this.validinfo=true
					}
				}
			}		
		},
		created(){
			
				if(this.$route.params.message){
					this.message = this.$route.params.message
					this.validinfo=true
				}

			this.$http.get('http://192.168.1.11:3000/users').then((response) => {
				console.log(response.body[0].login.username)
				this.data = response
			}, (response) => {
				alert('failed')
			});


		}
	}
</script>

<style scoped>
	.bg{
		width: 100%;
		height: 100vh;



	}
	.logheading,.options{
		color: rgba(0,0,0,0.6);
		text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
		0px -5px 35px rgba(255,255,255,0.3);
	}
	.loghead2{
		background: #f44336;
		color: rgba(255, 255, 255, .87)
	}

	.myForm{
		min-width:300px;
		min-height:300px;
		padding-top:5%;
		width: 60%;
		margin:0 auto;
		height:260px;
	}
	.Login{
		min-width:350px;
		min-height:300px;
		margin:0 auto;
		padding-top:10%;
		text-align:center;
		width: 50%;
	}
	.limiter{
		background:white;	
		padding:30px;
	}


	.md-body-1:hover{
		cursor:pointer;
		color:black;
	}
	.options{
		padding-top:8%;
	}
	.alert{
		 position: absolute;
  left: 50%;
  top: 27%;
  transform: translate(-50%, -50%);
	}
</style>


