// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import VueRouter from 'vue-router'
import VueMaterial from 'vue-material'
import VueResource from 'vue-resource'
import Add from './components/Additem'
import Login from './components/Login'
import NavApp from './components/NavApp'


Vue.use(VueRouter)
Vue.use(VueMaterial)
Vue.material.registerTheme('default', {
  primary: 'black',
  accent: 'red',
  warn: 'red',
  background: 'white'
})
Vue.use(VueResource)

const routes = [

{ path: '/', component: Login,name: 'login' },
{ path: '/main', component: NavApp, name: 'main',  meta: { requiresAuth: true } },
{ path: '/add', component: Add, name: 'add',  meta: { requiresAuth: true } }


]
const router = new VueRouter({
	mode: 'history',
	base:__dirname,
  routes // short for routes: routes
})
router.beforeEach((to, from, next) => {
	
	if(to.meta.requiresAuth){

		const userID = (window.localStorage.getItem('id'))
		console.log(userID, "userid")
		if(userID || userID == 0){
			next()
		}else{
			next({name:'login',params:{message:'Please log in to continue'}})
		}
	}
  next()
})

/* eslint-disable no-new */
const app = new Vue({
	el: '#app',
	router,
	render: h => h(App)
})



